/** Выбранная дата */
export interface SelectedDate {
  /** Одиночное значение/Начало диапазона */
  from: Date;

  /** Окончание диапазона */
  to?: Date;
}

/** Параметры дня */
export interface DayValue {
  /** Дата */
  value: Date;

  /** Заблокирован? */
  disabled?: boolean;

  /** Сегодня? */
  today?: boolean;

  /** Выбран? */
  selected?: boolean;

  /** Выходной? */
  weekend?: boolean;

  /** Внутри диапазона? */
  inRange?: boolean;

  /** Есть метка? */
  mark?: boolean;
}

/** Параметры месяца */
export interface MonthValue {
  /** Название месяца */
  name: string;

  /** Индекс месяца, от нуля */
  value: number;

  /** Год, к которому относится месяц */
  year: number;

  /** Заблокирован? */
  disabled?: boolean;

  /** Выбран? */
  selected?: boolean;

  /** В месяце находится сегодняшний день? */
  today?: boolean;
}

/** Параметры года */
export interface YearValue {
  /** Значение года */
  value: number;

  /** Выбран? */
  selected?: boolean;

  /** Заблокирован? */
  disabled?: boolean;

  /** В году находится сегодняшний день? */
  today?: boolean;
}

/** Страницы календаря */
export enum CalendarPage {
  /** Дни */
  Days,

  /** Месяцы */
  Months,

  /** Годы */
  Years
}

/** Параметры начальной страницы на которой открывать календарь */
export interface CalendarInitialState {
  /** Дата, на которой открыть календарь */
  date: Date;

  /** Начальная страница */
  page?: CalendarPage;
}

/** Параметры локализации календаря */
export interface LocaleOptions {
  /** Индекс начала недели */
  weekStartsOn: number;

  /** Выходные */
  weekends: Set<number>;

  /** Полные названия месяцев */
  longMonths: string[];

  /** Сокращенные названия месяцев */
  shortMonths: string[];

  /** Сокращенные названия дней недели */
  shortWeekdays: string[];
}

export const DEFAULT_LOCALE_OPTIONS: LocaleOptions = {
  weekStartsOn: 1,
  weekends: new Set([5, 6]),
  longMonths: ['Январь', 'Февраль', 'Март', 'Апрель', 'Май', 'Июнь', 'Июль', 'Август', 'Сентябрь', 'Октябрь', 'Ноябрь', 'Декабрь'],
  shortMonths: ['Янв', 'Фев', 'Мар', 'Апр', 'Май', 'Июн', 'Июл', 'Авг', 'Сен', 'Окт', 'Ноя', 'Дек'],
  shortWeekdays: ['П', 'В', 'С', 'Ч', 'П', 'С', 'В']
};
